.contacts {
  position: relative;
  top: -97px;
  z-index: 5;

  padding-bottom: 35px;

  background-color: @light-yellow;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.contacts__address-title {
  position: relative;

  width: 145px;
  margin: 0;
  margin-bottom: 32px;
  padding-top: 37px;
  padding-left: 65px;

  font-family: "BloggerSans-Bold", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 800;
  line-height: 22px;
  text-transform: uppercase;
  color: @dark-blue;

  &::before {
    content: "";

    position: absolute;
    left: 0;
    bottom: 2px;

    width: 45px;
    height: 45px;

    background: @dark-blue url("../../img/contacts-location.svg") no-repeat center;
    background-size: 35%;
    border-radius: 50%;
  }
}

.contacts__button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 24px 21px 24px;

  font-family: "BloggerSans-Bold", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 800;
  line-height: 18px;
  text-align: center;
  color: @dark-blue;
  text-decoration: none;

  border-radius: 10px;
  border: 2px solid @dark-blue;

  &--icon {
    fill: @dark-blue;
  }

  &:hover,
  &:focus {
    color: #ffffff;

    background-color: #192144;
  }

  &:active {
    color: fade(#ffffff, 30%);
  }
}

.contacts__button:hover > .contacts__button&--icon,
.contacts__button:focus > .contacts__button&--icon {
  fill: #ffffff;
}

.contacts__button:active > .contacts__button--icon {
  fill-opacity: 0.3;
}

@media (min-width: @tablet-width) {
  .contacts {
    padding-bottom: 85px;
  }

  .contacts__address-title {
    width: 430px;
    margin-bottom: 64px;
    padding-top: 70px;
    padding-left: 130px;

    &::before {
      left: 35px;
      bottom: -24px;

      width: 68px;
      height: 68px;
    }
  }

  .contacts__button {
    margin-left: 35px;
    margin-right: 35px;
  }
}

@media (min-width: @desktop-width) {
  .contacts__address {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 55px;
  }

  .contacts__address-title {
    padding-top: 0;
    margin-bottom: 0;
    margin-left: 225px;

    font-size: 28px;
    line-height: 28px;

    &::before {
      top: -24px;
    }
  }

  .contacts__button {
    box-sizing: border-box;
    width: 320px;
    margin: 0;
    margin-right: 75px;
    padding: 30px 35px;

    font-size: 24px;
    line-height: 24px;
  }
}
